code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

.onLoad {
	background: #f3904f;
	height: 200vh;
	width: 100vw;
	display: flex;
	justify-content: center;
	padding-top: 33vh;
	position: absolute;
	z-index: 10;
	top: 0;
	color: white;
}

.onLoadLogoBox {
	font-family: 'Secular One', sans-serif;
	font-size: 4rem;
	display: flex;
	text-transform: uppercase;
	/* border: orangered 2px solid; */
}

.onLoadLogoBox p {
	font-size: 17.5px;
	letter-spacing: 4px;
	color: #f3904f;
	background-color: white;
	padding: 2px;
}

.fadeOut {
	-webkit-animation: fadeout 4s;
	        animation: fadeout 4s;
	-webkit-animation-fill-mode: forwards;
	        animation-fill-mode: forwards;
	transition-timing-function: ease-out;
	-webkit-animation-delay: 2s;
	        animation-delay: 2s;
}

@-webkit-keyframes fadeout {
	from {
		left: 0;
		opacity: 1;
		height: 200vh;
	}
	to {
		left: 100%;
		opacity: 0;
		height: 100vh;
	}
}

@keyframes fadeout {
	from {
		left: 0;
		opacity: 1;
		height: 200vh;
	}
	to {
		left: 100%;
		opacity: 0;
		height: 100vh;
	}
}

