/**********************  
  MAIN
**********************/

.form {
	width: 100%;
	display: grid;
	grid-template-columns: 3fr 1fr;
	border: 8px solid #3b4371;
}

form input,
form button {
	font-size: 2rem;
	line-height: 1.3;
	font-family: 'Secular One', sans-serif;
}

form input {
	padding: 0 1rem;
	border: 3px solid #f3904f;
}

form button {
	background-color: #3b4371;
	border: 3px solid #333;
	color: white;
}

form button:active {
	background-color: #333;
}

/* .tasks {
	border: 2px orangered solid;
} */

.task {
	/* border: 1px orangered solid; */
	font-family: 'Kanit', sans-serif;
	line-height: 1.6;
	display: flex;
	align-items: center;
	box-shadow: 0px 2px 1.5px 0px rgb(59, 67, 113);
}

.label {
	font-size: 2rem;
	cursor: pointer;
}

.taskComplete {
	text-decoration: line-through;
}

.taskCompleteBox {
	background-color: rgba(51, 51, 51, 0.2);
}

.trashIcon {
	font-size: 1.2rem;
	cursor: pointer;
}

.trashIcon:hover,
.trashIcon:focus {
	color: #e74c3c;
}

.delButton {
	background: transparent;
	border: none;
}

.nonVisual {
	position: absolute;
	width: 2px;
	font-size: 1px;
	color: transparent;
}

.checkbox {
	position: absolute !important;
	height: 1px;
	width: 1px;
	overflow: hidden;
	clip: rect(1px, 1px, 1px, 1px);
}

.checkbox + .label {
	display: block;
	position: relative;
	padding: 0 1rem;
}

.checkbox + .label::before {
	content: '';
	position: relative;
	display: inline-block;
	margin-right: 10px;
	width: 20px;
	height: 20px;
	background: #3b4371;
	border: 1px solid #333;
}

.checkbox:checked + .label::before {
	background: #3b4371;
}

.checkbox:checked + .label::after {
	content: '';
	position: absolute;
	top: 20px;
	left: 19px;
	border-left: 2px solid #f3904f;
	border-bottom: 2px solid #f3904f;
	height: 6px;
	width: 13px;
	transform: rotate(-45deg);
}

.checkbox:focus + label::before {
	outline: 1px solid #5d9dd5;
	box-shadow: 0 0 8px #5e9ed6;
}

@media (max-width: 1000px) {
	.form {
		display: grid;
		grid-template-columns: 1fr;
		border: 8px solid #3b4371;
	}

	form input {
		text-align: center;
	}
}

@media (max-width: 530px) {
	form input,
	form button {
		font-size: 1.4rem;
	}

	.label {
		font-size: 1.5rem;
	}

	.checkbox:checked + .label::after {
		top: 12.4px;
	}
}

/**********************  
  FOOTER
**********************/

footer {
